<template>
  <div class="pickerwrap">
    <mt-picker
      :slots="dataList"
      :visible-item-count="5"
      :show-toolbar="true"
      ref="picker"
      value-key="cName"
    >
      <span class="onleft"></span>
      <span class="oncenter"></span>
      <span @click="handleConfirm" class="sure"></span>
    </mt-picker>
  </div>
</template>
<script>
export default {
  name: "kindpicker",
  data() {
    return {
      dateSlots: [
        // slotstagListapi
        /* {
          flex: 1,
          values: this.$store.state.tagList,
          className: 'slot1',
          textAlign: 'center'
        } */
      ],
      kinds: [],
      currentTags: {}
    };
  },
  created() {
    // createpicker,
    this.kinds = [
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      ""
    ];
  },
  computed: {
    dataList() {
      let mydata = [""];
      this.kinds.forEach(item => {
        mydata.push(item);
      });
      let dateSlots = [
        {
          flex: 1,
          values: mydata,
          className: "slot1",
          textAlign: "center"
        }
      ];
      return dateSlots;
    }
  },
  methods: {
    handleConfirm() {
      this.currentTags = this.$refs.picker.getValues()[0];
      this.$emit("listenkind", this.currentTags);
    }
  }
};
</script>
<style lang="scss">
.pickerwrap {
  width: 100%;
  .picker {
    .picker-toolbar {
      display: flex;
      justify-content: flex-end;
      .onleft {
        flex: 25%;
      }
      .oncenter {
        flex: 50%;
        font-size: 18px;
        margin-top: 5px;
      }
      .sure {
        flex: 25%;
        color: red;
        border: 0px;
        font-size: 18px;
        margin-top: 5px;
      }
    }
    //.picker-slot-wrapper,
    .picker-item {
      backface-visibility: hidden;
      width: 100%;
    }
  }
}
</style>